.section2 {
    position: relative;
}

.button-center-left {
    position: absolute;
    top: 50%;
    left: 0%;
    /* transform: translate(-50%, -50%); */
    width: 80%;
    padding: 15px 0px !important;
    border-radius: 15px !important;
}

.button-center-right {
    position: absolute;
    top: 50%;
    right: 0%;
    /* transform: translate(50%, -50%); */
    width: 80%;
    padding: 15px 0px !important;
    border-radius: 15px !important;
}

.motorPadding {
    padding: 8% 15%;
}

.ui.fade.animated.button .hidden.content i {
    font-size: 22px;
}

.buyNow {
    /* text-align: center !important; */
    margin: 0px !important;
    color: #fff !important;
}

.featureTitle {
    font-size: 24px !important;
    font-weight: bold;
    margin: 0px !important;
}

.featureDesc {
    margin: 0px !important;
}

.section3 {
    background-color: #f4f4f4;
    padding: 100px 0px;
}

.warranty-container {
    width: 40% !important;
    padding: 10% 0px;
}

.footer-icons {
    font-size: 22px !important;
    color: #fff;
    background-color: #898989;
    padding: 10px;
    border-radius: 50%;
    margin-left: 15px;
    margin-top: 20px;
}

.logoImage {
    height: 50px !important;
    width: auto !important;
}

.warranty {
    text-transform: uppercase !important;
    font-weight: 600 !important;
    letter-spacing: 4px;
}

.column a {
    color: #0a0a0a;
}

input[type="radio"] {
    height: 16px;
    padding: 0 0 0 18px;
}

@media only screen and (max-width: 1200px) {
    .top-h1-section2 {
        font-size: 35px !important;
    }
}

@media only screen and (max-width: 979px) {}

@media only screen and (max-width: 767px) {}

@media only screen and (max-width: 480px) {
    .motorColorName {
        display: none !important;
    }

    .btnBuySm {
        width: 100% !important;
    }

    .ui.center.aligned.header.margin-top-h1-section2 {
        margin-top: 0px;
        padding-top: 20px !important;
        padding-bottom: 50px !important;
        font-size: 28px !important;
        font-weight: 600 !important;
        letter-spacing: 0px;
    }
}

@media only screen and (max-width: 320px) {
    .motorColorName {
        display: none !important;
    }
}